/* eslint-disable jsx-a11y/anchor-is-valid */
import React from 'react';
import cn from 'classnames';
import styles from './styles.module.scss';
import switchIcon from '../../assets/images/switch_icon.png';
import questionIcon from '../../assets/images/question_icon.png';
import listIcon from '../../assets/images/list_icon.png';
import calendarIcon from '../../assets/images/calendar_icon.png';
import { dividData } from '../../data';

const headerData = [
  { title: 'Инструмент', connectedField: 'name', id: 0 },
  { title: 'Купить до', connectedField: 'buyBefore', id: 1 },
  { title: 'Закрытие реестра', connectedField: 'lastDate', id: 2 },
  { title: 'Размер купона', connectedField: 'couponValue', id: 3 },
  { title: 'Цена', connectedField: 'price', id: 4 },
  { title: 'Пустое поле', connectedField: 'addField1', id: 5 },
  { title: 'Пустое поле', connectedField: 'addField2', id: 6 },
];
const initialState = {
  divid: dividData,
  active: null,
  sort: null,
};

class DividendList extends React.Component {
  state = initialState;
  clickHandler = (name) => () => {
    const { active, sort, divid } = this.state;
    const newData = divid.concat();
    if (active !== name) {
      newData.sort((a, b) => (a[name] < b[name] ? 1 : -1));
      this.setState({
        divid: newData,
        active: name,
        sort: 'asc',
      });
    } else if (active === name && sort === 'asc') {
      newData.sort((a, b) => (b[name] < a[name] ? 1 : -1));
      this.setState({
        divid: newData,
        sort: 'desc',
      });
    } else {
      this.setState({
        active: null,
        sort: null,
      });
    }
  };
  render() {
    const { divid, active, sort } = this.state;
    return (
      <div className={styles.container}>
        <div className={styles.title}>
          <div className={styles.title__buttons}>
            <button className={`${styles.button} ${styles.button__list}`}>
              <img className={styles.button__icon} src={listIcon} alt='List Icon' />
              Списком
            </button>
            <button className={`${styles.button} ${styles.button__calendar}`}>
              <img className={styles.button__icon} src={calendarIcon} alt='Calendar Icon' />
              Календарь
            </button>
          </div>
          <h3 className={styles.title__year}>2020</h3>
          <div className={styles.subtitle}>
            <img className={styles.subtitle__switch} src={switchIcon} alt='Switch' />
            <span className={styles.subtitle__text}>Голубые фишки</span>
            <img className={styles.subtitle__question} src={questionIcon} alt='Question' />
          </div>
        </div>
        <div className={styles.table}>
          <ul className={styles.table__header}>
            {headerData.map((item) => (
              <li
                onClick={this.clickHandler(item.connectedField)}
                className={cn(styles.header__item, styles.column, {
                  [styles['header__item-add']]:
                    item.connectedField === 'addField1' || item.connectedField === 'addField2',
                })}
                key={item.id}
              >
                <span className={styles.item__text}>{item.title}</span>
                <span
                  className={cn({
                    [styles.triangle__down]:
                      sort === null || sort === 'asc' || item.connectedField !== active,
                    [styles.triangle__up]: item.connectedField === active && sort === 'desc',
                    [styles.active__down]: item.connectedField === active && sort === 'asc',
                    [styles.active__up]: item.connectedField === active && sort === 'desc',
                  })}
                />
              </li>
            ))}
          </ul>
          <ul className={styles.table__content}>
            {divid.map((item, index) => (
              <li className={styles.content__item} key={`${item.name}-${index}`}>
                <div className={cn(styles.item__field, styles.column)}>
                  <h4 className={styles.item__title}>{item.name}</h4>
                </div>
                <div className={cn(styles.item__field, styles.column)}>
                  <h5 className={styles.field__name}>Купить до</h5>
                  <p className={styles.field__value}>{item.buyBefore}</p>
                </div>
                <div className={cn(styles.item__field, styles.column)}>
                  <h5 className={styles.field__name}>Закрытие реестра</h5>
                  <p className={styles.field__value}>{item.lastDate}</p>
                </div>
                <div className={cn(styles.item__field, styles.column)}>
                  <h5 className={styles.field__name}>Размер купона</h5>
                  <p className={styles.field__value}>{item.couponValue}</p>
                </div>
                <div className={cn(styles.item__field, styles.column)}>
                  <h5 className={styles.field__name}>Цена</h5>
                  <p className={styles.field__value}>{item.price}</p>
                </div>
                <div className={cn(styles.item__field, styles.column, styles['item__field-add'])}>
                  <p className={styles.field__value}>{item.addField1}</p>
                </div>
                <div className={cn(styles.item__field, styles.column, styles['item__field-add'])}>
                  <p className={styles.field__value}>{item.addField2}</p>
                </div>
              </li>
            ))}
          </ul>
        </div>
        <button className={styles['big-button']}>Показать ещё</button>
      </div>
    );
  }
}
export default DividendList;
